import React from "react";
import SEOHelmet from "../components/SEOHelmet";
import { useLanguage } from "../context/LanguageContext"; // useLanguage 경로 수정

const Contact = () => {
  const { language } = useLanguage();
  const { t } = useLanguage();

  // 언어에 따른 Google Form URL
  const formUrls: Record<string, string> = {
    en: "https://docs.google.com/forms/d/e/1FAIpQLSd6dR5yUJ8piAwQEjag0oHPijTYBqbhwIeoyKwWXO5fpaeprQ/viewform?embedded=true",
    ko: "https://docs.google.com/forms/d/e/1FAIpQLSdCdagkaMY2380TZDty67WxqEl2bsqFFkDgPulGXukVxpxPPQ/viewform?embedded=true",
  };

  const formUrl = formUrls[language]; // 현재 언어에 맞는 URL 선택

  return (
    <div>
      <SEOHelmet
        title="문의하기 Contact - ROKIT Genomics"
        description="궁금하신 사항은 언제든 로킷제노믹스로 직접 문의해 주세요. 전문성을 기반으로 답변드리겠습니다."
        image="/image/genomics-thumbnail.jpg"
        url="https://rokitgenomics/contact"
      />
      <div className="bg-neutral-200 section-main">
        <div className="section-main-container">
          <div className="title-container">
            <h2 className="text-center">{t("ContactUs", "title")}</h2>
            <p className="text-center">{t("ContactUs", "sub-text")}</p>
          </div>
          <div className="flex items-center justify-center">
            <iframe src={formUrl} width="1200" height="1400">
              Loading...
            </iframe>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Contact;
