import React, { useState } from "react";
import SEOHelmet from "../components/SEOHelmet";
import { useLanguage } from "../context/LanguageContext"; // useLanguage 경로 수정
import Genome from "./ServicesGenome";
import Transcriptome from "./ServicesTranscriptome";
import Epigenome from "./ServicesEpigenome";
import Metagenome from "./ServicesMetagenome";
import NavigateButton from "../components/NavigateButton";

const NGS = () => {
  const { t } = useLanguage();

  return (
    <div>
      <SEOHelmet
        title="차세대염기서열분석 NGS - ROKIT Genomics"
        description="DNA, RNA, Epigenome, Metagenome 다양한 분야의 Next Generation Sequencing 서비스를 제공합니다."
        image="/image/genomics-thumbnail.jpg"
        url="https://rokitgenomics/services/ngs"
      />
      <div className="bg-neutral-200 section-main">
        <div className="section-main-container">
          <h2>{t("Service-NGS", "main-title")}</h2>
          <p>{t("Service-NGS", "main-text")}</p>
          <NavigateButton
            to="/contact"
            label={t("Button", "contact")}
            className="button-bold"
          />
        </div>
      </div>
      <Genome />
      <Transcriptome />
      <Epigenome />
      <Metagenome />
      <div className="bg-gradient-to-tr from-orange-200 to-orange-50 section-main">
        <div className="text-center section-main-container">
          <h2>{t("Service-NGS", "contact-main-title")}</h2>
          <p>{t("Service-NGS", "contact-main-text")}</p>
          <NavigateButton
            to="/contact"
            label={t("Button", "contact")}
            className="button-bold"
          />
        </div>
      </div>
    </div>
  );
};

export default NGS;
