import React from "react";
import SEOHelmet from "../components/SEOHelmet";
import { useLanguage } from "../context/LanguageContext"; // useLanguage 경로 수정
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Table from "../components/CareersTable";

const Careers = () => {
  const { t } = useLanguage();

  return (
    <div>
      <SEOHelmet
        title="채용 정보 Careers - ROKIT Genomics"
        description="로킷제노믹스와 함께할 인재를 모십니다. 진행 중인 채용 정보를 알려드립니다."
        image="/image/genomics-thumbnail.jpg"
        url="https://rokitgenomics/company/careers"
      />
      <div className="bg-neutral-200 section-main">
        <div className="section-main-container">
          <div className="title-container">
            <h2>{t("Careers", "main-title")}</h2>
            <p>{t("Careers", "sub-title")}</p>
          </div>
        </div>
      </div>
      <div className="bg-white section-main">
        <div className="section-main-container">
          <Table />
        </div>
      </div>
    </div>
  );
};

export default Careers;
