import React, { ReactNode } from "react";

interface NewsCardProps {
  title: string;
  children: ReactNode;
  image: string;
  link?: string; // 선택적 링크 (클릭 시 이동)
}

const NewsCard: React.FC<NewsCardProps> = ({
  title,
  children,
  image,
  link,
}) => {
  const handleClick = () => {
    if (link) {
      window.open(link, "_blank", "noopener,noreferrer");
    }
  };

  return (
    <div
      className={`flex flex-col items-center w-full overflow-hidden bg-white md:flex-row h-full ${
        link ? "cursor-pointer" : "cursor-default"
      }`}
      onClick={handleClick}
    >
      {/* 텍스트 섹션 (왼쪽) */}
      <div className="w-full pr-6 md:w-2/3">{children}</div>

      {/* 이미지 섹션 (오른쪽) */}
      <div className="w-full md:w-1/3">
        <img
          src={image}
          alt={title}
          className="object-cover w-full h-48 rounded-xl"
        />
      </div>
    </div>
  );
};

export default NewsCard;
