import React, { useState, useEffect } from "react";
import SEOHelmet from "../components/SEOHelmet";
import { useLanguage } from "../context/LanguageContext"; // useLanguage 경로 수정
import { Slideshow } from "../components/Slideshow";
import ReactPlayer from "react-player";
import NavigateButton from "../components/NavigateButton";
import newsList from "../data/newsList";
import NewsCard from "../components/NewsCard";
import { experiment, analysis, singlecell } from "../assets/homeImages";

const Home = () => {
  const { t } = useLanguage();
  const slideData = newsList.slice().reverse().slice(0, 3);
  const [isActive, setIsActive] = useState(false);

  return (
    <div>
      <SEOHelmet
        title="로킷제노믹스 ROKIT Genomics"
        description="단일 세포 전사체 및 오믹스 분석 전문 기업 로킷제노믹스입니다. Single cell RNA sequencing 서비스를 제공합니다."
        image="/image/genomics-thumbnail.jpg"
        url="https://rokitgenomics.com/"
      />
      <div className="w-full">
        <div className="flex w-full h-[600px] md:h-[900px] relative overflow-hidden">
          <div
            data-aos="fade-up"
            className="max-w-[1200px] mx-auto z-10 pt-24 text-center"
          >
            <h2>{t("Home", "main-title")}</h2>
            <p>{t("Home", "main-text")}</p>
          </div>
          <ReactPlayer
            url={process.env.PUBLIC_URL + "/videos/glassball.mp4"}
            playing={true}
            muted={true}
            loop={true}
            playsinline={true}
            controls={false}
            className="absolute z-0 transform -translate-x-1/2 -translate-y-1/2 top-1/2 left-1/2 scale-[1.7] md:scale-[2.5] 2xl:scale-[2.7]"
            style={{
              objectFit: "cover",
            }}
            config={{
              file: {
                attributes: {
                  autoPlay: true,
                  muted: true,
                  playsInline: true,
                },
              },
            }}
          />
        </div>
      </div>

      <div className="bg-white section-main">
        <div className="space-y-4 section-main-container">
          <h2 data-aos="fade-up">{t("Home", "section-1")}</h2>
          <div className="flex flex-col w-full space-x-0 space-y-6 md:space-y-0 md:space-x-6 md:flex-row">
            <div className="flex flex-col items-center justify-between w-full space-y-4">
              <div
                className="relative w-full h-full overflow-hidden group rounded-2xl"
                onClick={() => setIsActive(!isActive)}
              >
                {/* 이미지 */}
                <img
                  src={experiment}
                  alt="Sample"
                  className="object-cover w-full h-full transition-transform duration-500 group-hover:scale-110"
                />

                {/* 텍스트 오버레이 */}
                <div
                  className={`absolute inset-0 flex items-center justify-center text-center bg-black bg-opacity-50 transition-opacity duration-500 ${
                    isActive ? "opacity-100" : "opacity-0"
                  } group-hover:opacity-100`}
                >
                  <p className="text-base font-normal text-white">
                    {t("Home", "section-1-text-1")}
                  </p>
                </div>
              </div>
              <p className="text-2xl font-semibold text-center">
                {t("Home", "section-1-title-1")}
              </p>
            </div>
            <div className="flex flex-col items-center justify-between w-full space-y-4">
              <div
                className="relative w-full h-full overflow-hidden rounded-2xl group"
                onClick={() => setIsActive(!isActive)}
              >
                {/* 이미지 */}
                <img
                  src={analysis}
                  alt="Sample"
                  className="object-cover w-full h-full transition-transform duration-500 group-hover:scale-110"
                />

                {/* 텍스트 오버레이 */}
                <div
                  className={`absolute inset-0 flex items-center justify-center text-center bg-black bg-opacity-50 transition-opacity duration-500 ${
                    isActive ? "opacity-100" : "opacity-0"
                  } group-hover:opacity-100`}
                >
                  <p className="text-base font-normal text-white">
                    {t("Home", "section-1-text-2")}
                  </p>
                </div>
              </div>
              <p className="text-2xl font-semibold text-center">
                {t("Home", "section-1-title-2")}
              </p>
            </div>
            <div className="flex flex-col items-center justify-between w-full space-y-4">
              <div
                className="relative w-full h-full overflow-hidden rounded-2xl group"
                onClick={() => setIsActive(!isActive)}
              >
                {/* 이미지 */}
                <img
                  src={singlecell}
                  alt="Sample"
                  className="object-cover w-full h-full transition-transform duration-500 group-hover:scale-110"
                />

                {/* 텍스트 오버레이 */}
                <div
                  className={`absolute inset-0 flex items-center flex-col justify-center text-center bg-black bg-opacity-50 transition-opacity duration-500 ${
                    isActive ? "opacity-100" : "opacity-0"
                  } group-hover:opacity-100`}
                >
                  <p className="text-base font-normal text-white">
                    {t("Home", "section-1-text-3")}
                  </p>
                  <NavigateButton
                    to="/services/scrna"
                    label={t("Button", "learn-more")}
                    className="button-bold"
                  />
                </div>
              </div>
              <p className="text-2xl font-semibold text-center">
                {t("Home", "section-1-title-3")}
              </p>
            </div>
          </div>
        </div>
      </div>

      <div className="bg-white section-main">
        <div className="space-y-4 section-main-container">
          <h2 data-aos="fade-up">{t("Home", "section-2")}</h2>
          <Slideshow interval={2500}>
            {slideData.map((news, index) => {
              // 다국어 키 변환
              const [namespaceBold, keyBold] = news.content.bold.split(".");
              const [namespaceTrun, keyTrun] =
                news.content.truncated.split(".");
              const [namespaceSmall, keySmall] = news.content.small.split(".");

              return (
                <NewsCard
                  key={index}
                  title={news.title}
                  image={news.image}
                  link={news.link}
                >
                  <p className="p-bold">{t(namespaceBold, keyBold)}</p>
                  <p className="p-trun">{t(namespaceTrun, keyTrun)}</p>
                  <p className="p-small">{t(namespaceSmall, keySmall)}</p>
                </NewsCard>
              );
            })}
          </Slideshow>
        </div>
      </div>

      <div className="bg-white section-main h-[400px]">
        <div className="space-y-4 text-center section-main-container">
          <h2 data-aos="fade-up">{t("Home", "section-3")}</h2>
          <NavigateButton
            to="/contact"
            label={t("Button", "contact")}
            className="button-bold"
          />
        </div>
      </div>
    </div>
  );
};

export default Home;
