import React from "react";
import SEOHelmet from "../components/SEOHelmet";
import { useLanguage } from "../context/LanguageContext"; // useLanguage 경로 수정
import NewsCard from "../components/NewsCard";
import newsList from "../data/newsList"; // ✅ newsList 불러오기

const PrCenter = () => {
  const { t } = useLanguage();

  return (
    <div>
      <SEOHelmet
        title="보도자료 PR Center - ROKIT Genomics"
        description="로킷제노믹스의 주요 언론 보도 소식을 전해드립니다."
        image="/image/genomics-thumbnail.jpg"
        url="https://rokitgenomics/prcenter"
      />
      <div className="bg-neutral-200 section-main">
        <div className="section-main-container">
          <div className="title-container">
            <h2>{t("PrCenter", "main-title")}</h2>
            <p>{t("PrCenter", "main-text")}</p>
          </div>
        </div>
      </div>
      <div className="bg-white section-main">
        <div className="section-main-container">
          <div className="w-full space-y-16 md:space-y-12">
            {newsList
              .slice()
              .reverse()
              .map((news, index) => {
                // ✅ 구조 분해 할당을 사용하여 다국어 키를 변수로 저장
                const [namespaceBold, keyBold] = news.content.bold.split(".");
                const [namespaceTrun, keyTrun] =
                  news.content.truncated.split(".");
                const [namespaceSmall, keySmall] =
                  news.content.small.split(".");

                return (
                  <NewsCard
                    key={index}
                    title={news.title}
                    image={news.image}
                    link={news.link}
                  >
                    <p className="p-bold">{t(namespaceBold, keyBold)}</p>
                    <p className="p-trun">{t(namespaceTrun, keyTrun)}</p>
                    <p className="p-small">{t(namespaceSmall, keySmall)}</p>
                  </NewsCard>
                );
              })}
          </div>
        </div>
      </div>
    </div>
  );
};

export default PrCenter;
