import React from "react";
import SEOHelmet from "../components/SEOHelmet";
import { useLanguage } from "../context/LanguageContext"; // useLanguage 경로 수정
import Table from "../components/NoticeTable";

const Notice = () => {
  const { t } = useLanguage();

  return (
    <div>
      <SEOHelmet
        title="공지사항 Notice - ROKIT Genomics"
        description="기업 관련 주요 공지사항을 알려드립니다."
        image="/image/genomics-thumbnail.jpg"
        url="https://rokitgenomics/company/notice"
      />
      <div className="bg-neutral-200 section-main">
        <div className="section-main-container">
          <div className="title-container">
            <h2>{t("Notice", "main-title")}</h2>
            <p>{t("Notice", "sub-title")}</p>
          </div>
        </div>
      </div>
      <div className="bg-white section-main">
        <div className="section-main-container">
          <Table />
        </div>
      </div>
    </div>
  );
};

export default Notice;
